import f_table from "@/components/table";
import f_button from "@/components/button";
import edit_input from "@/components/edit_input";
export default {
  components: {
    f_table,
    edit_input,
    f_button
  },
  data() {
    return {
        button_list: [
        {
          type: "primary",
          text: "编辑",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "归还人", k: "张三" },
        { read: true, v: "归还时间", k: "2019-01-12 09:00:12" },
        {
          read: true,
          v: "备注",
          k: "备注信息",
          hide_border: true
        }
      ],
      edit_input_list1: [
        { read: true, v: "创建时间", k: "2019-01-12 09:00:12" },
        {
          read: true,
          v: "最后更新",
          k: "2019-01-12 09:00:12",
          hide_border: true
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "序号", min_w: true },
                { td: "设备名称", td_104_w: true },
                { td: "固资编号" },
                { td: "设备状态", td_70_w: true },
                { td: "图片数量", td_70_w: true }
              ]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "1", min_w: true },
                { td: "轨道检测仪", td_104_w: true },
                { td: "M767672000" },
                { td: "正常", td_70_w: true },
                { td: "2", td_70_w: true }
              ]
            },
            {
              td_list: [
                { td: "1", min_w: true },
                { td: "轨道检测仪", td_104_w: true },
                { td: "M767672000" },
              ]
            }
          ]
        }
      }
    };
  },
  methods: {
      btn_call_back(){
          console.log(1)
      },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



