import f_button from "@/components/button";
import edit_input from "@/components/edit_input";
import upload_file from "@/components/upload_file";
import edit_select from "@/components/edit_select";
import edit_textarea from "@/components/edit_textarea";
import result from "@/components/result";
import f_table from "@/components/table";
export default {
  components: {
    edit_input,
    f_button,
    upload_file,
    edit_select,
    edit_textarea,
    f_table,
    result
  },
  data() {
    return {
      upload_file_info: {
        text: "图片",
        required: true,
        class: "img40"
      },
      show_device_modal: false,
      show_result: false,
      edit_input_list: [
        { v: "设备名称", k: "轨道检测仪", read: true },
        { v: "固资编号", k: "M767672", read: true }
      ],
      edit_textarea: {
        required: true,
        title: "备注",
        placeholder: "请输入备注信息"
      },
      button_list: [
        {
          type: "primary",
          text: "归还",
          call_back: true
        }
      ],
      edit_select: [
        {
          title: "设备状态",
          default_val: "",
          required: true,
          placeholder: "请选择设备状态",
          list: [
            { key: "正常", value: "正常" },
            { key: "正常1", value: "正常1" }
          ]
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "序号", min_w: true },
                { td: "设备名称", td_104_w: true },
                { td: "固资编号" },
                { td: "设备状态", td_70_w: true },
                { td: "图片数量", td_70_w: true }
              ]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "1", min_w: true },
                { td: "轨道检测仪", td_104_w: true },
                { td: "M767672000" },
                { td: "正常", td_70_w: true },
                { td: "2", td_70_w: true }
              ]
            }
          ]
        }
      },

      confirm_info: {
        title: "设备出库",
        content: "设备出库成功，是否提交保存？",
        show: false
      },
      result_info: {
        title: "归还成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>，您已经成功归还2台设备',
        buttons: [
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          },
          {
            type: "default",
            text: "关闭",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    hide_device_modal() {
      this.show_device_modal = false;
    },
    get_call_back() {
      this.show_result = true;
    },
    confirm_back(res) {},
    go_page(url) {
      this.$router.push(url);
    }
  }
};



