import edit_input from "../../../../edit_input";
import f_button from "../../../../button";

export default {
  components: {
    edit_input,
    f_button
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "编辑",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "钢轨廓形仪", k: "使用中", no_arrow: true },
        { read: true, v: "M0192123", k: "" },
        { read: true, v: "北一", k: "", hide_border: true }
      ],
      edit_input_list1: [
        { read: true, v: "固资编号", k: "M0192123" },
        { read: true, v: "财务卡片号", k: "M019212309" },
        { read: true, v: "研究院编号", k: "M0192123786", hide_border: true }
      ],
      edit_input_list2: [
        { read: true, v: "设备序列", k: "廓形仪" },
        { read: true, v: "固定资产名称", k: "M001091205" },
        { read: true, v: "规格型号", k: "RC240C" },
        { read: true, v: "机身唯一代码", k: "001091205", hide_border: true }
      ],
      edit_input_list3: [
        { read: true, v: "增加方式", k: "增加方式" },
        { read: true, v: "利润中心", k: "利润中心名称" },
        { read: true, v: "部门", k: "部门" },
        { read: true, v: "责任人", k: "责任人" },
        { read: true, v: "使用人", k: "使用人" },
        { read: true, v: "验收领用日", k: "验收领用日", hide_border: true }
      ],
      edit_input_list4: [
        { read: true, v: "资本化日期", k: "资本化日期" },
        { read: true, v: "折旧剩余周期", k: "12" },
        { read: true, v: "质保开始日期", k: "质保开始日期" },
        { read: true, v: "质保结束日期", k: "质保结束日期" },
        { read: true, v: "质保期（天）", k: "质保期（天）" },
        { read: true, v: "质保到期日", k: "质保到期日", hide_border: true }
      ],
      edit_input_list5: [
        { read: true, v: "供应商名称", k: "供应商名称", hide_border: true }
      ],
      edit_input_list6: [
        { read: true, v: "原值", k: "23万" },
        { read: true, v: "每月折旧金额", k: "2000元" },
        { read: true, v: "本年度折旧月数", k: "1" },
        { read: true, v: "本年度折旧金额", k: "2000元", hide_border: true }
      ],
      edit_input_list7: [
        { read: true, v: "流转次数", k: "12" },
        { read: true, v: "维修次数", k: "17", hide_border: true }
      ],
      edit_input_list8: [
        { read: true, v: "备注", k: "备注内容", hide_border: true }
      ],
      edit_input_list9: [
        { read: true, v: "创建时间", k: "2019-01-12 09:00:12" },
        {
          read: true,
          v: "更新时间",
          k: "2019-01-12 09:00:12",
          hide_border: true
        }
      ]
    };
  },
  methods: {
    get_call_back() {
      this.go_page("/add_device");
    },
    go_page(url) {
      this.$router.push(url);
    },
    input_back(res) {
      console.log(res);
      if (res == 0) {
        //流转
        this.go_page("/loop_list");
      } else if (res == 1) {
        //维修
        this.go_page("/service_list");
      }
    }
  }
};



