import f_table from "@/components/table";
import edit_input from "@/components/edit_input";
export default {
  components: {
    f_table,
    edit_input
  },
  data() {
    return {
      edit_input_list: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "领用人", k: "张三" },
        { read: true, v: "领用时间", k: "2019-01-12 09:00:12" },
        {
          read: true,
          v: "备注",
          k: "备注信息",
          hide_border: true
        }
      ],
      edit_input_list1: [
        { read: true, v: "创建时间", k: "2019-01-12 09:00:12" },
        {
          read: true,
          v: "最后更新",
          k: "2019-01-12 09:00:12",
          hide_border: true
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "序号" }, { td: "设备名称" }, { td: "数量" }]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "1" }, { td: "轨道检测仪" }, { td: "2" }]
            },
            {
              td_list: [{ td: "1" }, { td: "轨道检测仪" }, { td: "2" }]
            }
          ]
        }
      }
    };
  },
  methods: {
    go_page(url) {
      this.$router.push(url);
    }
  }
};



