import f_button from "@/components/button";
import edit_select from "@/components/edit_select";
import upload_file from "@/components/upload_file";
import f_confirm from "@/components/confirm";
import edit_input from "@/components/edit_input";
import result from "@/components/result";
export default {
  components: {
    edit_input,
    upload_file,
    edit_select,
    f_confirm,
    result,
    f_button
  },
  data() {
    return {
      show_result: false,
      button_list: [
        {
          type: "primary",
          text: "提交盘点",
          call_back: true
        }
      ],
      result_info: {
        title: "提交成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>的车轮廓形仪盘点成功。',
        buttons: [
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          },
          {
            type: "default",
            text: "关闭",
            link: "/"
          }
        ]
      },
      confirm_info: {
        show: false,
        title: "盘点",
        content: "确定提交盘点信息?"
      },
      edit_select: [
        {
          title: "设备状态",
          default_val: "",
          placeholder: "请选择设备状态",
          w120:true,
          list: [
            { key: "正常", value: "正常" },
            { key: "正常1", value: "正常1" }
          ]
        }
      ],
      upload_file_info: {
        text: "机身编号",
        class: "img40",
        w120: true
      },
      upload_file_info1: {
        text: "固资标签",
        class: "img40",
        w120: true
      },
      upload_file_info2: {
        text: "仪器与配件",
        class: "img40",
        w120: true
      },
      upload_file_info3: {
        text: "仪器与本人证明",
        class: "img40",
        w120: true
      },
      edit_input_list: [
        { read: true, v: "设备名称", k: "钢轨廓形仪" ,w120:true,},
        { read: true, v: "设备序列", k: "M0192123" ,w120:true,},
        { read: true, v: "盘点人", k: "张三", hide_border: true ,w120:true,}
      ]
    };
  },
  methods: {
    btn_call_back() {
      this.confirm_info.show = true;
    },
    go_page(url) {
      this.$router.push(url);
    },
    confirm_back() {
      this.show_result = true;
    }
  }
};



